<template>
  <div class="reconciliation-container">
    <el-card>
      <div slot="header" class="card-header">
        <span>对账管理</span>
        <div>
          <el-button type="primary" @click="showAutoReconcileDialog">对账</el-button>
        </div>
      </div>

      <!-- 搜索条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="科目">
          <el-select v-model="searchForm.accountId" placeholder="请选择科目" filterable clearable style="width: 200px">
            <el-option
              v-for="subject in subjectList"
              :key="subject.id"
              :label="subject.code + ' - ' + subject.name"
              :value="subject.id.toString()">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="对账日期">
          <el-date-picker
            v-model="searchForm.reconciliationDate"
            type="date"
            placeholder="选择对账日期"
            value-format="yyyy-MM-dd"
            clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="searchForm.status" placeholder="请选择状态" clearable>
            <el-option label="未对账" :value="0"></el-option>
            <el-option label="已对账" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="resetSearch">重置</el-button>
        </el-form-item>
      </el-form>

      <!-- 数据表格 -->
      <el-table
        v-loading="loading"
        :data="reconciliationList"
        style="width: 100%"
      >
        <el-table-column prop="accountId" label="科目ID" width="100"></el-table-column>
        <el-table-column label="科目名称" width="200">
          <template slot-scope="scope">
            <span v-if="getSubjectName(scope.row.accountId)">{{ getSubjectName(scope.row.accountId) }}</span>
            <span v-else style="color: #909399;">-</span>
          </template>
        </el-table-column>
        <el-table-column prop="reconciliationDate" label="对账日期" ></el-table-column>
        <el-table-column prop="bookBalance" label="账面余额" >
          <template slot-scope="scope">
            {{ formatMoney(scope.row.bookBalance) }}
          </template>
        </el-table-column>
        <el-table-column prop="statementBalance" label="对账单余额">
          <template slot-scope="scope">
            {{ formatMoney(scope.row.statementBalance) }}
          </template>
        </el-table-column>
        <el-table-column prop="difference" label="差额">
          <template slot-scope="scope">
            {{ formatMoney(scope.row.difference) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态" >
          <template slot-scope="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ getStatusText(scope.row.status) }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" type="info" @click="handleView(scope.row)">查看</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageInfo.total"
          :current-page="pageInfo.pageNum"
          :page-size="pageInfo.pageSize"
          :page-sizes="[10, 20, 50, 100]"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
        ></el-pagination>
      </div>

      <!-- 对账对话框 -->
      <el-dialog
        title="对账"
        :visible.sync="autoReconcileDialogVisible"
        width="500px"
        :close-on-click-modal="false"
      >
        <el-form :model="autoReconcileForm" :rules="autoReconcileRules" ref="autoReconcileForm" label-width="120px">
          <el-form-item label="会计科目" prop="subjectId">
            <el-select v-model="autoReconcileForm.subjectId" placeholder="请选择会计科目" filterable style="width: 100%">
              <el-option
                v-for="subject in subjectList"
                :key="subject.id"
                :label="subject.code + ' - ' + subject.name"
                :value="subject.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="对账日期" prop="reconciliationDate">
            <el-date-picker
              v-model="autoReconcileForm.reconciliationDate"
              type="date"
              placeholder="选择对账日期"
              value-format="yyyy-MM-dd"
              style="width: 100%"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="对账单余额" prop="statementBalance">
            <el-input
              v-model="autoReconcileForm.statementBalance"
              placeholder="请输入对账单余额"
            ></el-input>
          </el-form-item>
          <el-form-item label="期初日期">
            <el-date-picker
              v-model="autoReconcileForm.startDate"
              type="date"
              placeholder="选择期初日期（可选）"
              value-format="yyyy-MM-dd"
              style="width: 100%"
            ></el-date-picker>
            <div style="color: #909399; font-size: 12px; margin-top: 5px;">不选择则期初余额默认为0</div>
          </el-form-item>
          <el-form-item label="对账说明">
            <el-input
              type="textarea"
              v-model="autoReconcileForm.remark"
              placeholder="可填写本次对账的说明"
              :rows="2"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="autoReconcileDialogVisible = false">取消</el-button>
          <el-button type="primary" @click="handleAutoReconcile" :loading="autoReconcileLoading">开始对账</el-button>
        </div>
      </el-dialog>

      <!-- 查看对账详情对话框 -->
      <el-dialog
        title="对账详情"
        :visible.sync="viewDialogVisible"
        width="900px"
        :close-on-click-modal="false"
      >
        <div v-if="viewData">
          <!-- 基本信息 -->
          <el-card shadow="never" style="margin-bottom: 20px;">
            <div slot="header">
              <span style="font-weight: bold;">基本信息</span>
            </div>
            <el-descriptions :column="2" border>
              <el-descriptions-item label="科目编码">{{ viewData.subject?.code }}</el-descriptions-item>
              <el-descriptions-item label="科目名称">{{ viewData.subject?.name }}</el-descriptions-item>
              <el-descriptions-item label="对账日期">{{ formatDate(viewData.reconciliation?.reconciliationDate) }}</el-descriptions-item>
              <el-descriptions-item label="状态">
                <el-tag :type="getStatusType(viewData.reconciliation?.status)">{{ getStatusText(viewData.reconciliation?.status) }}</el-tag>
              </el-descriptions-item>
              <el-descriptions-item label="对账说明" :span="2">{{ viewData.reconciliation?.remark || '-' }}</el-descriptions-item>
            </el-descriptions>
          </el-card>

          <!-- 账面余额计算明细 -->
          <el-card shadow="never" style="margin-bottom: 20px;" v-if="viewData.balanceDetail">
            <div slot="header">
              <span style="font-weight: bold;">账面余额计算明细</span>
            </div>
            <el-descriptions :column="2" border>
              <el-descriptions-item label="余额方向">{{ viewData.balanceDetail.balanceDirectionText }}</el-descriptions-item>
              <el-descriptions-item label="借方总额">{{ formatMoney(viewData.balanceDetail.debitTotal) }}</el-descriptions-item>
              <el-descriptions-item label="贷方总额">{{ formatMoney(viewData.balanceDetail.creditTotal) }}</el-descriptions-item>
              <el-descriptions-item label="计算出的账面余额">
                <span style="font-weight: bold; color: #409EFF;">{{ formatMoney(viewData.balanceDetail.calculatedBalance) }}</span>
              </el-descriptions-item>
            </el-descriptions>
          </el-card>

          <!-- 对账比对 -->
          <el-card shadow="never" style="margin-bottom: 20px;" v-if="viewData.comparison">
            <div slot="header">
              <span style="font-weight: bold;">对账比对</span>
            </div>
            <el-descriptions :column="2" border>
              <el-descriptions-item label="账面余额">
                <span style="font-weight: bold;">{{ formatMoney(viewData.comparison.bookBalance) }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="对账单余额">
                <span style="font-weight: bold;">{{ formatMoney(viewData.comparison.statementBalance) }}</span>
              </el-descriptions-item>
              <el-descriptions-item label="差额" :span="2">
                <span :style="{ fontWeight: 'bold', color: Math.abs(viewData.comparison.difference) > 0 ? '#F56C6C' : '#67C23A' }">
                  {{ formatMoney(viewData.comparison.difference) }}
                  <span v-if="viewData.comparison.difference > 0">（对账单余额大于账面余额）</span>
                  <span v-else-if="viewData.comparison.difference < 0">（对账单余额小于账面余额）</span>
                  <span v-else>（余额一致）</span>
                </span>
              </el-descriptions-item>
            </el-descriptions>
          </el-card>

          <!-- 对账期间的凭证明细 -->
          <el-card shadow="never" v-if="viewData.entryDetails && viewData.entryDetails.length > 0">
            <div slot="header">
              <span style="font-weight: bold;">对账期间的凭证明细（共 {{ viewData.entryDetails.length }} 笔）</span>
            </div>
            <el-table :data="viewData.entryDetails" border stripe max-height="400">
              <el-table-column prop="voucherNo" label="凭证编号" width="120"></el-table-column>
              <el-table-column prop="voucherDate" label="凭证日期" width="110">
                <template slot-scope="scope">
                  {{ formatDate(scope.row.voucherDate) }}
                </template>
              </el-table-column>
              <el-table-column prop="summary" label="摘要" show-overflow-tooltip></el-table-column>
              <el-table-column prop="debitAmount" label="借方金额" width="120" align="right">
                <template slot-scope="scope">
                  {{ formatMoney(scope.row.debitAmount) }}
                </template>
              </el-table-column>
              <el-table-column prop="creditAmount" label="贷方金额" width="120" align="right">
                <template slot-scope="scope">
                  {{ formatMoney(scope.row.creditAmount) }}
                </template>
              </el-table-column>
              <el-table-column prop="remark" label="备注" show-overflow-tooltip></el-table-column>
            </el-table>
          </el-card>
          <el-card shadow="never" v-else>
            <div style="text-align: center; color: #909399; padding: 20px;">
              对账期间无已过账的凭证明细
            </div>
          </el-card>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="viewDialogVisible = false">关闭</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import request from '@/api/request';
import { subjectAPI } from '@/api/api';

export default {
  name: "Reconciliation",
  data() {
    return {
      loading: false,
      searchForm: {
        accountId: '',
        reconciliationDate: '',
        status: ''
      },
      reconciliationList: [],
      subjectList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      autoReconcileDialogVisible: false,
      autoReconcileLoading: false,
      autoReconcileForm: {
        subjectId: null,
        reconciliationDate: '',
        startDate: '',
        statementBalance: '',
        remark: ''
      },
      autoReconcileRules: {
        subjectId: [
          { required: true, message: '请选择会计科目', trigger: 'change' }
        ],
        reconciliationDate: [
          { required: true, message: '请选择对账日期', trigger: 'change' }
        ],
        statementBalance: [
          { required: true, message: '请输入对账单余额', trigger: 'blur' }
        ]
      },
      viewDialogVisible: false,
      viewData: null
    };
  },
  mounted() {
    // 先加载科目列表，再加载对账列表，避免科目名称映射时 subjectList 为空
    this.loadSubjectList().then(() => {
    this.getList();
    });
  },
  methods: {
    // 加载科目列表
    async loadSubjectList() {
      try {
        const res = await subjectAPI.getSubjectTree();
        if (res.code === 200) {
          // 扁平化科目树
          this.subjectList = this.flattenSubjects(res.data || []);
        }
      } catch (error) {
        console.error('加载科目列表失败', error);
      }
    },

    // 扁平化科目树
    flattenSubjects(subjects) {
      let result = [];
      subjects.forEach(subject => {
        result.push(subject);
        if (subject.children && subject.children.length > 0) {
          result = result.concat(this.flattenSubjects(subject.children));
        }
      });
      return result;
    },

    // 获取科目名称
    getSubjectName(accountId) {
      if (!accountId) return '';
      const subject = this.subjectList.find(s => s.id.toString() === accountId.toString());
      return subject ? `${subject.code} - ${subject.name}` : '';
    },

    // 获取列表数据
    async getList() {
      this.loading = true;
      try {
        const params = {
          pageNum: this.pageInfo.pageNum,
          pageSize: this.pageInfo.pageSize
        };
        if (this.searchForm.accountId) {
          params.accountId = this.searchForm.accountId;
        }
        if (this.searchForm.reconciliationDate) {
          params.reconciliationDate = this.searchForm.reconciliationDate;
        }
        if (this.searchForm.status !== '') {
          params.status = this.searchForm.status;
        }

        const response = await request.get('/reconciliation/page', { params });
        const responseData = response.data || {};
        this.reconciliationList = responseData.records || [];
        this.pageInfo.total = responseData.total || 0;
      } catch (error) {
        this.$message.error('获取对账列表失败：' + (error.response?.data?.message || error.message));
      } finally {
        this.loading = false;
      }
    },

    // 查询
    handleSearch() {
      this.pageInfo.pageNum = 1;
      this.getList();
    },

    // 重置查询条件
    resetSearch() {
      this.searchForm = {
        accountId: '',
        reconciliationDate: '',
        status: ''
      };
      this.getList();
    },

    // 分页变化
    handleCurrentChange(pageNum) {
      this.pageInfo.pageNum = pageNum;
      this.getList();
    },

    handleSizeChange(pageSize) {
      this.pageInfo.pageSize = pageSize;
      this.pageInfo.pageNum = 1;
      this.getList();
    },

    // 显示自动对账对话框
    showAutoReconcileDialog() {
      // 如果科目列表还未加载，先加载科目列表
      const ensureSubjectsLoaded = this.subjectList && this.subjectList.length
        ? Promise.resolve()
        : this.loadSubjectList();

      ensureSubjectsLoaded.then(() => {
      this.autoReconcileForm = {
        subjectId: null,
        reconciliationDate: '',
        startDate: '',
        statementBalance: '',
        remark: ''
      };
      this.$nextTick(() => {
        if (this.$refs.autoReconcileForm) {
          this.$refs.autoReconcileForm.resetFields();
        }
      });
      this.autoReconcileDialogVisible = true;
      });
    },

    // 执行对账
    async handleAutoReconcile() {
      this.$refs.autoReconcileForm.validate(async (valid) => {
        if (valid) {
          this.autoReconcileLoading = true;
          try {
            const params = {
              subjectId: this.autoReconcileForm.subjectId,
              reconciliationDate: this.autoReconcileForm.reconciliationDate,
              statementBalance: this.autoReconcileForm.statementBalance,
              remark: this.autoReconcileForm.remark
            };

            const res = await request.post('/reconciliation/auto', null, { params });
            if (res.code === 200) {
              this.$message.success('对账成功');
              this.autoReconcileDialogVisible = false;
              this.getList();
            } else {
              this.$message.error(res.message || '对账失败');
            }
          } catch (error) {
            this.$message.error('对账失败：' + (error.response?.data?.message || error.message));
          } finally {
            this.autoReconcileLoading = false;
          }
        }
      });
    },

    // 查看对账详情
    async handleView(row) {
      try {
        const res = await request.get(`/reconciliation/detail/${row.id}`);
        if (res.code === 200) {
          this.viewData = res.data;
          this.viewDialogVisible = true;
        } else {
          this.$message.error(res.message || '获取对账详情失败');
        }
      } catch (error) {
        this.$message.error('获取对账详情失败：' + (error.response?.data?.message || error.message));
      }
    },

    // 删除对账记录
    async handleDelete(row) {
      this.$confirm('确定要删除该对账记录吗？删除后无法恢复。', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          const res = await request.delete(`/reconciliation/${row.id}`);
          if (res.code === 200) {
          this.$message.success('删除成功');
          this.getList();
          } else {
            this.$message.error(res.message || '删除失败');
          }
        } catch (error) {
          this.$message.error('删除失败：' + (error.response?.data?.message || error.message));
        }
      }).catch(() => {
        this.$message.info('已取消操作');
      });
    },

    // 格式化金额
    formatMoney(value) {
      if (!value && value !== 0) return '0.00';
      return Number(value).toLocaleString('zh-CN', {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2
      });
    },

    // 格式化日期
    formatDate(date) {
      if (!date) return '-';
      try {
        const d = new Date(date);
        if (isNaN(d.getTime())) return '-';
        const year = d.getFullYear();
        const month = String(d.getMonth() + 1).padStart(2, '0');
        const day = String(d.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
      } catch (e) {
        return '-';
      }
    },

    // 获取状态文本
    getStatusText(status) {
      const statusMap = {
        0: '未对账',
        1: '已对账'
      };
      return statusMap[status] || '未知';
    },

    // 获取状态标签类型
    getStatusType(status) {
      const typeMap = {
        0: 'warning',
        1: 'success'
      };
      return typeMap[status] || 'default';
    }
  }
};
</script>

<style scoped>
.reconciliation-container {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-form {
  margin-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
}
</style>

